<template>
  <div class="myEvaluate">
    <!--    <van-nav-bar title="提交评价" :border="false" left-arrow @click-left="goBack">-->
    <!--      <div slot="right" class="submit" @click="clickRight">提交</div>-->
    <!--    </van-nav-bar>-->
    <navigation-bar @onLeftClick="goBack" pageName="评价晒单">
      <template v-slot:nav-right>
        <div class="submit" @click="clickRight">提交</div>
      </template>
    </navigation-bar>
    <div class="content" v-if="detail">
      <div class="box">

        <div class="top">
          <div class="title">商品评价</div>
          <div v-for="(skuItem,index) in detail.billItem.shop.skuList" :key="index">
            <div class="skuItemBox" v-if="index<showMoreSku">
              <div class="evaluate_box">
                <div class="good_img">
                  <img :src="skuItem.spuPicUrl" alt="">
                </div>
                <div class="evaluate">

                  <div class="evaluate_info">
                    <div class="star">
                      <img :src="clickIcon" alt="" v-for="(item) in clickList.slice(0, skuItem.evalLevel)" :key="item"
                        @click="selectScore(item,index)">
                      <img :src="normalIcon" alt="" v-for="(item) in clickList.slice(skuItem.evalLevel, 5)" :key="item"
                        @click="selectScore(item,index)">
                    </div>
                    <div class="evaluate_name">{{skuItem.evalLevelText}}</div>
                  </div>
                </div>
              </div>
              <div class="line"></div>
              <div class="reason">
                <textarea name="" id="" placeholder="亲，收到的货品满足你的期待吗？对货品的包装还满意吗？快点和大家分享你的购物心得吧～" v-model="skuItem.evalContent"
                  maxlength="100"></textarea>
              </div>
              <!-- <div class="image-box">
                <add-image v-on:imageAddAction="showAddSheetControll(index)"
                  v-on:imageMapChange="upDateImageData(newImageData,index)" v-on:changeUploadStatus="changeStatus"
                  :is-upload="isUpload" :uploadIndex="index" ref="addImageComponent"></add-image>
              </div> -->
              <section class="uploadImgBox">
<!--                <div class="uploadImgListBox">-->
<!--                  <div-->
<!--                    class="uploadImgItem"-->
<!--                    v-for="(item,imgIndex) in skuItem.uploadImgList"-->
<!--                    :key="imgIndex"-->
<!--                    v-if="imgIndex < 4 "-->
<!--                  >-->
<!--                    <img :src="item.imgUrl" class="uplaodImg" />-->
<!--                    <div class="rightImg">-->
<!--                      <img :src="deleteImg" v-if="item.show" class="uplaodImg" @click="deleteUploadImg(imgIndex,index)" />-->
<!--                    </div>-->
<!--                  </div>-->
<!--                  <div class="uplaodImgBox" v-if="skuItem.uploadImgList.length <= 3">-->
<!--                    <img :src="uploadImg" class="uplaodImg" @click="showAddSheetControll(index)" />-->
<!--                  </div>-->
<!--                </div>-->
                <van-uploader v-model="fileList" multiple :after-read="afterRead" :max-count="4" accept="image/*"></van-uploader>

              </section>


              <div class="line"></div>
            </div>
          </div>

          <div class="goodsEvaluateBottom" v-if="detail.billItem.shop.skuList.length>1" @click="showMore()">
            <div class="bottomTitle" v-if="showMoreSku==1">共{{detail.billItem.shop.skuList.length}}件商品可评价</div>
            <div class="bottomTitle" v-if="showMoreSku!=1">收起</div>
            <img class="iconImg" :src="showMoreSku!=1?upIcon:downIcon" alt="">
          </div>
          <div class="noGoodsEvaluateBottom" v-if="detail.billItem.shop.skuList.length==1"></div>
        </div>


        <div class="bottom">

          <div class="evaluate_box">
            <div class="evaluateTitle">
              店铺评价
            </div>
            <div class="evaluate">
              <div class="evaluate_info">
                <div class="star">
                  <img :src="clickIcon" alt=""
                    v-for="(item) in clickList.slice(0, detail.billItem.shop.logisticsEvalLevel)" :key="item"
                    @click="logisticsEvalLevelSelectScore(item)">
                  <img :src="normalIcon" alt=""
                    v-for="(item) in clickList.slice(detail.billItem.shop.logisticsEvalLevel, 5)" :key="item"
                    @click="logisticsEvalLevelSelectScore(item)">
                </div>
                <div class="evaluate_name">{{detail.billItem.shop.logisticsEvalLevelText}}</div>
              </div>
            </div>
          </div>
          <div class="noMarginLine"></div>

          <div class="evaluate_box">
            <div class="evaluateTitle">
              配送评价
            </div>
            <div class="evaluate">
              <div class="evaluate_info">
                <div class="star">
                  <img :src="clickIcon" alt="" v-for="(item) in clickList.slice(0, detail.billItem.shop.serveEvalLevel)"
                    :key="item" @click="serveEvalLevelSelectScore(item)">
                  <img :src="normalIcon" alt=""
                    v-for="(item) in clickList.slice(detail.billItem.shop.serveEvalLevel, 5)" :key="item"
                    @click="serveEvalLevelSelectScore(item)">
                </div>
                <div class="evaluate_name">{{detail.billItem.shop.serveEvalLevelText}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="anonymousBox">
          <img class="anonymousSelect" :src="detail.billItem.shop.isAnonymous?indicatorSelectedImg:indicatorNoSelectImg" alt="" @click="anonymousSelect()">
          <div class="anonymousName" @click="anonymousSelect()">
            匿名评价
          </div>
        </div>
      </div>
    </div>
    <van-action-sheet v-model="showAddSheet" :actions="actions" cancel-text="取消" :round="false" close-on-click-action
      @select="addSelect" />
  </div>
</template>

<script>
  import axios from "axios";
  import qs, { stringify } from "qs";
  import { ImagePreview, Toast ,Uploader } from "vant";
  import { store } from "@/utils/storage";
  import { goodsEvalService,goodsChildOrdersEvalService } from "../../services/evaluateService";
  import {uploadFileService} from "../../services/commonPhoneService";
  import AddImageVue from "../../components/addImage/AddImage.vue";
  import clickIcon from "@/assets/images/common/icon_evaluate_red.png";
  import normalIcon from "@/assets/images/common/icon_evaluate_gray.png";
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  import upIcon from "@/assets/images/mall/mall_icon_up.png";
  import downIcon from "@/assets/images/mall/mall_icon_down.png";
  import indicatorNoSelectImg from '@/assets/images/default/default_bottom.png'
    //指示器图片已选择
  import indicatorSelectedImg from '@/assets/images/default/default_active.png'
  import Vue from 'vue';
  Vue.use(Uploader);
  export default {
    name: "myEvaluate",
    data() {
      return {
        // evaluateInfo: "",
        clickList: [1, 2, 3, 4, 5],
        // levalNum: 5,
        // imgList: [],
        remindList: ["极差", "一般", "不错", "满意", "超赞"],
        detail: null,
        showAddSheet: false,
        actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
        clickIcon: clickIcon,
        normalIcon: normalIcon,
        upIcon: upIcon,
        downIcon: downIcon,
        indicatorNoSelectImg: indicatorNoSelectImg,
        indicatorSelectedImg: indicatorSelectedImg,
        icon: {
          up: require("@/assets/images/mall/mall_icon_up.png"), //上 返回
          down: require("@/assets/images/mall/mall_icon_down.png"), //下 返回
        },
        deleteImg: require("@/assets/images/common/icon_delete_image.png"),
        uploadImg: require("@/assets/images/common/upload_img.png"),
        // 是否从订单详情页面进入，如果是，则提交成功之后直接到订单列表页面
        isFromDetail: false,
        isUpload: false,
        showMoreSku: 1,
        goodsIndex:0,
        // isAnonymous:true
        fileList: []
      };
    },
    created() {
      this.detail = this.$route.params.detail
      this.detail.billItem.shop.skuList.map((skuItem) => {
        skuItem.evalLevel = 5;//防止传给申请售后的图片为本地图片
        skuItem.evalLevelText = this.remindList[4];
        skuItem.uploadImgList = [];
        skuItem.evalPicUrl = [];
        skuItem.imgIndex = 0;
        skuItem.evalContent = "";
      });
      this.detail.billItem.shop.logisticsEvalLevel = 5;
      this.detail.billItem.shop.logisticsEvalLevelText = this.remindList[4];
      this.detail.billItem.shop.serveEvalLevel = 5;
      this.detail.billItem.shop.serveEvalLevelText = this.remindList[4];
      this.detail.billItem.shop.isAnonymous = false;
      // = {
      //   spuPicUrl:
      //     "https://yanxuan-item.nosdn.127.net/2ce4616cbfd096c6878be0df37adc91b.png",
      //   spuCode: "343613707998986240",
      //   shopCode: "319019907922763776",
      //   orderCode: "347273308967264256C2",
      //   attrValueContent: "净含量:28ml×6片"
      // };
    },
    components: {
      "add-image": AddImageVue,
      NavigationBar
    },
    computed: {
      getRemind(levalNum) {
        return this.remindList[levalNum - 1];
      }
    },
    beforeRouteEnter(to, from, next) {
      next(vm => {
        if (from.name == 'orderDetails') {
          vm.isFromDetail = true
        }
      })
    },
    methods: {
      afterRead(file){
        console.log("uploadFile fileList: "+JSON.stringify(this.fileList))
        let formData = new FormData();
        formData.append("image", file.content);
          var params = {};
          params.file=formData;
          params.source = "yzApp";
          params.business = "complPic";
        let http = axios.create({
          timeout: 15000,//默认15s超时
          headers: {
            "Content_type":"multipart/form-data",
          },
        });
          http({
            method:'POST',
            url: `${store.get("custUrl.homeUrl")}xwj-sys-media/media/upload/uploadFile`,
            data:qs.stringify(params),
            withCredentials: false,
          }).then((res)=>{
            console.log(JSON.stringify(res));
          }).catch((err)=>{
            console.log(err);
          });
          // uploadFileService(params)
          //     .then(res => {
          //         console.log("上传成功了 "+JSON.stringify(res));
          //     })
          //     .catch(function (error) {
          //         // this.$toast(error);
          //       console.log("不对劲 "+error);
          //     });
      },
      /**
       * 页面返回事件
       */
      goBack() {
        if (this.$store.state.preview.previewId) {
          this.$store.dispatch('setPreviewId', '');
        }
        this.$router.go(-1);
      },

      /**
       * 选择商品评分
       */
      selectScore(num, index) {
        this.detail.billItem.shop.skuList[index].evalLevel = num;
        this.detail.billItem.shop.skuList[index].evalLevelText = this.remindList[num - 1];
        this.$forceUpdate();
      },
      /**
      * 选择店铺评分
      */
      logisticsEvalLevelSelectScore(num) {
        this.detail.billItem.shop.logisticsEvalLevel = num;
        this.detail.billItem.shop.logisticsEvalLevelText = this.remindList[num - 1];
        this.$forceUpdate();
      },

      /**
      * 选择服务评分
      */
      serveEvalLevelSelectScore(num) {
        this.detail.billItem.shop.serveEvalLevel = num;
        this.detail.billItem.shop.serveEvalLevelText = this.remindList[num - 1];
        this.$forceUpdate();
      },

      showMore() {
        if (this.showMoreSku == 1) {
          this.showMoreSku = 100;
        } else {
          this.showMoreSku = 1;
        }
      },
      anonymousSelect(){
        this.detail.billItem.shop.isAnonymous = !this.detail.billItem.shop.isAnonymous;
        this.$forceUpdate();
      },
      /**
       * 提交评价
       */
       clickRight() {


        //刘炯丽和吴慧贤确认 评价内容及图片非必填
        // for(var i=0;i<this.detail.billItem.shop.skuList.length;i++)
        // {
        //   var skuItem = this.detail.billItem.shop.skuList[i];
        //   if (skuItem.evalContent.length == 0) {
        //   this.$toast("商品评价不能为空");
        //   return;
        //   }
        // }

        for(var i=0;i<this.detail.billItem.shop.skuList.length;i++)
        {
          var skuItem = this.detail.billItem.shop.skuList[i];
          if (skuItem.evalPicUrl.length < skuItem.uploadImgList.length) {
          this.$toast("图片正在上传中，请稍候");
          return;
          }
        }

        



        // var img = [];
        // for (var i = 0; i < this.imgList.length; i++) {
        //   img.push({
        //     picUrl: this.imgList[i]
        //   });
        // }
        var data = {
          ownerCode: this.$userInfo.OWNER_CODE, // "2018122909483465412",
          ownerName: this.$userInfo.OWNER_NAME, //"5460_yz",
          ownerNickname: this.$userInfo.OWNER_NICK_NAME, //"5460",
          shopCode: this.detail.billItem.shop.shopCode,
          orderCode: this.detail.billItem.billNo,
          logisticsEvalLevel: this.detail.billItem.shop.logisticsEvalLevel,
          isAnonymous: this.detail.billItem.shop.isAnonymous?1:0,
          serveEvalLevel: this.detail.billItem.shop.serveEvalLevel,
          goodsEvalList: this.detail.billItem.shop.skuList
          // evalPicUrl: img
        };
        goodsChildOrdersEvalService(data)
          .then(res => {
            if (res.resultCode == "0") {
              if (res.data.pointIncome == 0||!res.data.pointIncome) {
                this.$toast("评价成功");
              } else {
                this.$toast("评价成功，获取" + res.data.pointIncome + "积分");
              }
              if (this.isFromDetail) {
                this.$router.go(-2)
              } else {
                this.$router.go(-1)
              }
            } else {
              this.$toast(res.msg);
            }
          })
          .catch(function (error) {
            this.$toast(error);
          });
      },

      // clickRight() {
      //   if (this.evalContent.length == 0) {
      //     this.$toast("商品评价不能为空");
      //     return;
      //   } else {
      //     this.isUpload = true;
      //   }
      // },

      changeStatus() {
        this.isUpload = false;
      },

      // 删除图片列表
    deleteUploadImg(imgIndex,index) {
      console.log("deleteUploadImg");
      this.detail.billItem.shop.skuList[index].uploadImgList.splice(imgIndex, 1);
      this.detail.billItem.shop.skuList[index].evalPicUrl.splice(imgIndex, 1);
      console.log(this.detail.billItem.shop);
      this.$forceUpdate();
      console.log("deleteUploadImg1");
    },

    /**
     * 调用摄像头插件进行拍照
     * @param
     **/
    useCamera(goodsIndex) {

      let that = this;
      this.$plugin.usePlugin("camera")(
        true,
        imageData => {
          if (this.$devicePlatform == "android") {
            //裁剪图片
            this.$plugin.usePlugin("crop")(
              imageData,
              url => {
                //success callback
                console.log("yuanl=> 裁剪url = " + url);
                url = url.split("?");
                var uploadImg = {
                  imgUrl:url[0],
                  imgIndex:that.detail.billItem.shop.skuList[goodsIndex].imgIndex,
                  show:false
                }
                that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.push(uploadImg);
                that.uploadFile(url[0], that.detail.billItem.shop.skuList[goodsIndex].imgIndex, goodsIndex);
                that.detail.billItem.shop.skuList[goodsIndex].imgIndex++;
                that.$forceUpdate();
              },
              error => {
                //fail callback
                console.log("Crop error. " + JSON.stringify(error));
                if (error.recapture == "true") {
                  console.log("调用相机重拍. ");
                  this.useCamera(goodsIndex);
                } else if (error.message == "User cancelled") {
                  console.log("用户取消裁剪操作. ");
                } else {
                  this.$toast("裁剪图片出现异常，请重试！");
                }
              }
            );
          } else {
            console.log("照相成功，开始上传");
            console.log(goodsIndex);
            var uploadImg = {
                  imgUrl:imageData,
                  imgIndex:that.detail.billItem.shop.skuList[goodsIndex].imgIndex,
                  show:false
                }
            that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.push(uploadImg);
            console.log(that.detail.billItem.shop.skuList);
            that.uploadFile(imageData, that.detail.billItem.shop.skuList[goodsIndex].imgIndex, goodsIndex);
            console.log("照相成功，开始上传1");
            that.detail.billItem.shop.skuList[goodsIndex].imgIndex++;
            that.$forceUpdate();
          }
        },
        error => {
          //Toast(error); //delete for ZHSQYYPT-6708
        }
        // options
      );
    },

    uploadFile(fileURL, index ,goodsIndex) {
      let that = this;
      let reqUrl = `${store.get(
        "custUrl.homeUrl"
      )}xwj-sys-media/media/upload/uploadFile`;
      console.log("照相成功，开始上传2");
      // let callBackGoodsIndex = goodsIndex;
      var success = function(result) {
        let resData = JSON.parse(result.response);

        console.log("qiao " + result + " JSON: " + JSON.stringify(resData));
        if (resData.resultCode == 0) {
          let con = {};
          con.index = index;
          con.picUrl = resData.data;
          //上传成功展示删除按钮
          that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.map((item)=>{
            if(item.imgIndex==index)
            {
              item.show = true;
            }
          })
          var uploadIndex = 0;
          for(var i = 0;i<that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.length;i++)
          {
            var item = that.detail.billItem.shop.skuList[goodsIndex].uploadImgList[i];
            if(item.imgIndex==index)
            {
              item.show = true;
              uploadIndex = i;
            }
            
          }
          that.detail.billItem.shop.skuList[goodsIndex].uploadImgList[uploadIndex].show = true;
          if(that.detail.billItem.shop.skuList[goodsIndex].evalPicUrl.length>uploadIndex){
            that.detail.billItem.shop.skuList[goodsIndex].evalPicUrl.splice(uploadIndex, 0, con);;
          }else
          {
            that.detail.billItem.shop.skuList[goodsIndex].evalPicUrl.push(con);
          }
          // that.detail.billItem.shop.skuList[goodsIndex].evalPicUrl.push(con);
          console.log("success");
          console.log(JSON.stringify(that.detail.billItem.shop));
          that.$forceUpdate();
        } else {
          that.uploadFile1(fileURL, index,goodsIndex);
        }
      };
      var fail = function(error) {
        console.log("fail");
        that.uploadFile1(fileURL, index,goodsIndex);
      };
      var options = new FileUploadOptions();
      var params = {};
      params.file = fileURL;
      params.source = "yzApp";
      params.business = "complPic";
      options.params = params;
      var headers = { contentType: "multipart/form-data" };
      options.headers = headers;

      // this.$plugin.usePlugin("fileTransfer")(fileURL,encodeURI(reqUrl),success,fail,options);
      var ft = new FileTransfer();
      console.log("照相成功，开始上传3");
      ft.upload(fileURL, encodeURI(reqUrl), success, fail, options);
    },

    uploadFile1(fileURL, index,goodsIndex) {
      let num = index;
      let that = this;
      let reqUrl = `${store.get(
        "custUrl.homeUrl"
      )}xwj-sys-media/media/upload/uploadFile`;
      var success = function(result) {
        let resData = JSON.parse(result.response);
        console.log("qiao " + result + " JSON: " + JSON.stringify(result));
        if (resData.resultCode == 0) {
          let con = {};
          con.index = index;
          con.picUrl = resData.data;
          //上传成功展示删除按钮
          var uploadIndex = 0;
          for(var i = 0;i<that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.length;i++)
          {
            var item = that.detail.billItem.shop.skuList[goodsIndex].uploadImgList[i];
            if(item.imgIndex==index)
            {
              item.show = true;
              uploadIndex = i;
            }
            
          }
          that.detail.billItem.shop.skuList[goodsIndex].uploadImgList[uploadIndex].show = true;
          if(that.detail.billItem.shop.skuList[goodsIndex].evalPicUrl.length>uploadIndex){
            that.detail.billItem.shop.skuList[goodsIndex].evalPicUrl.splice(uploadIndex, 0, con);;
          }else
          {
            that.detail.billItem.shop.skuList[goodsIndex].evalPicUrl.push(con);
          }
          that.$forceUpdate();
        } else {
          Toast("success图片上传失败");
          console.log(num);
          console.log(that.detail.billItem.shop.skuList[goodsIndex].uploadImgList);
          that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.splice(num, 1);
          that.detail.billItem.shop.skuList[goodsIndex].imgIndex = that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.length - 1;
          that.$forceUpdate();
          if (that.imgIndex < 0) {
            that.imgIndex = 0;
          }
          if (that.detail.billItem.shop.skuList[goodsIndex].evalPicUrl.length == 0) {
            that.detail.billItem.shop.skuList[goodsIndex].uploadImgList = [];
          }
        }
      };
      var fail = function(error) {
        Toast("fail图片上传失败");
        console.log(num);
        console.log(that.detail.billItem.shop.skuList[goodsIndex].uploadImgList);
        that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.splice(num, 1);
        that.detail.billItem.shop.skuList[goodsIndex].imgIndex = that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.length - 1;
        that.$forceUpdate();
        if (that.detail.billItem.shop.skuList[goodsIndex].imgIndex < 0) {
          that.detail.billItem.shop.skuList[goodsIndex].imgIndex = 0;
        }
        if (that.detail.billItem.shop.skuList[goodsIndex].evalPicUrl.length == 0) {
          that.detail.billItem.shop.skuList[goodsIndex].uploadImgList = [];
        }
      };
      var options = new FileUploadOptions();
      var params = {};
      params.file = fileURL;
      params.source = "yzApp";
      params.business = "complPic";
      options.params = params;
      var headers = { contentType: "multipart/form-data" };
      options.headers = headers;

      // this.$plugin.usePlugin("fileTransfer")(fileURL,encodeURI(reqUrl),success,fail,options);
      var ft = new FileTransfer();
      ft.upload(fileURL, encodeURI(reqUrl), success,  fail, options);
    },

    /**
     * 从本地相册进行上传
     **/
    getPicture(goodsIndex) {
      let that = this;
      let options = {
        quality: 90,
        destinationType: Camera.DestinationType.DATA_URL,
        sourceType: Camera.PictureSourceType.CAMERA, //照相机类型
        allowEdit: true,
        encodingType: Camera.EncodingType.JPEG,
        targetWdith: 100,
        targetHeight: 100,
        popoverOptions: CameraPopoverOptions,
        saveToPhotoAlbum: false
      };

      this.$plugin.usePlugin("getPictures")(
        4-that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.length,
        images => {
          images.forEach(imageData => {
            var uploadImg = {
                  imgUrl:imageData,
                  imgIndex:that.detail.billItem.shop.skuList[goodsIndex].imgIndex,
                  show:false
                }
            that.detail.billItem.shop.skuList[goodsIndex].uploadImgList.push(uploadImg);
            that.uploadFile(imageData, that.detail.billItem.shop.skuList[goodsIndex].imgIndex, goodsIndex);
            that.detail.billItem.shop.skuList[goodsIndex].imgIndex++;
            that.$forceUpdate();
          });
        },
        error => {
          //Toast(error); //delete for ZHSQYYPT-6708
        },
        options
      );
    },



      /**
       * 子组件更新已选图片集合监听函数
       **/
      upDateImageData(newImageData,index) {
        console.log("upDateImageData"+index);
        this.detail.billItem.shop.skuList[index].imgList = newImageData;
        this.submit();
      },

      /**
       * addImage组件添加照片时的监听函数父组件用来选择哪种图片获取方式(拍照或相册上传)
       **/
      showAddSheetControll(index) {
        this.showAddSheet = !this.showAddSheet;
        console.log("showAddSheetControll"+index);
        console.log("that.detail.billItem.shop."+this.detail.billItem.shop);
        console.log(JSON.stringify(this.detail.billItem.shop));
        this.goodsIndex = index;
      },

      /**
       * 选择当前图片来源方式(拍照或本地上传)
       * @param
       * item      Object   当前选择的方式对象
       * index     Number   当前选择的索引
       **/
      addSelect(item, index) {
        console.log(item);
      // console.log(this.maximumImagesCount);
      if (item.name == "拍照") {
        this.useCamera(this.goodsIndex);
      } else if (item.name == "从手机相册选择") {
        this.getPicture(this.goodsIndex);
      }
      },

      /**
       * 调用摄像头或者本地相册进行图片上传
       * @param
       **/
      getPictureData(selectIndex) {
        selectIndex
          ? this.$refs.addImageComponent[this.goodsIndex].getPicture()
          : this.$refs.addImageComponent[this.goodsIndex].useCamera();
      }
    }
  };
</script>

