import http from "../http";

//获取商品的所有评价
const getAllEvaluateService = (data) => {
    return http.get('xwj-commerce-sale/eval/getGoodsEvalListForOwner', data);
}

//获取商品的所有评价
const getEvalPicUrlService = (data) => {
    return http.get('xwj-commerce-sale/eval/getEvalPicUrl', data);
}

//对订单进行评价
const goodsEvalService = (data) => {
    return http.post('xwj-commerce-sale/eval/goodsEval', data);
}

//对子单进行评价
const goodsChildOrdersEvalService = (data) => {
    return http.post('xwj-commerce-sale/eval/goodsChildOrdersEval', data);
}


export {
    getAllEvaluateService,
    getEvalPicUrlService,
    goodsEvalService,
    goodsChildOrdersEvalService
}
