import http from "../http";


export { getPhoneListService, uploadFileService }

//获取用户小区列表
const getPhoneListService = (params) => {
    return new Promise((resolve, reject) => {
        http.post('hisense-systemServer/app/owner/mcommonTelephoneMgr/listNewCommonTelephone.do', params, true,"homeUrl").then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })

    });
}
//图片上传
const uploadFileService = (params) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-sys-media/media/upload/uploadFile', params, true,"homeUrl").then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })

    });
}