<template>
  <div>
    <ul class="image-list">
      <li
        v-for="(picUrl,picIndex) in selectPictures"
        v-bind:key="picIndex"
        v-bind:style="{width:imageWidthAndHeight+'px',height:imageWidthAndHeight+'px'}"
      >
        <img
          src="../../assets/images/find/find_white.png"
          class="delete-picture"
          alt
          @click="deletePicture(picIndex)"
        />
        <img :src="picUrl" @click="preview(picIndex)" alt />
      </li>
      <li v-if="accountAddImage && isShowAdd">
        <div class="add-image" @click="addAction">
          <van-icon name="plus" color="rgb(181, 181, 181)" />
        </div>
      </li>
    </ul>
    <Preview :images="images" ref="myPreview"></Preview>
  </div>
</template>

<script>
import { ImagePreview, Toast } from "vant";
import config from "../../common/config";
import Preview from "@/components/preview/Preview.vue";
import { store } from "../../utils/storage";
export default {
  name: "AddImage",
  props: {
    maxImageCount: {
      type: Number,
      default: 4
    },
    selectPictures: {
      // 存储上传成功的图片地址
      type: Array,
      default: function() {
        return [];
      }
    },
    isShowAdd: {
      type: Boolean,
      default: true
    },
    isUpload: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      // 图片预览属性
      images: [],
      uploadImageList: [],
      loading: ''
    };
  },
  components: {
    Preview: Preview
  },
  mounted() {
    //image 宽度高度自适应
    let ulDom = document.querySelector(".image-list");
    this.imageWidthAndHeight = ulDom.offsetWidth / 4;
  },
  computed: {
    //超过最大数不显示添加
    accountAddImage() {
      return this.selectPictures.length < this.maxImageCount;
    },

    //相册选择图片最大数值
    maximumImagesCount() {
      return this.maxImageCount - this.selectPictures.length;
    }
  },

  watch: {
    selectPictures(newValue, oldName) {
      // this.$emit("imageMapChange", this.selectPictures.slice(0));
    },
    isUpload(newValue, oldName) {
      if (newValue) {
        console.log(newValue);
        this.uploadFile();
        this.$emit("changeUploadStatus");
      }
    }
  },
  methods: {
    /**
     * 图片预览
     * @param
     * startPosition  Number   当前选中图片索引
     **/
    preview(startPosition) {
      this.images = this.selectPictures;
      this.$refs.myPreview.setIndex(startPosition);
      this.$refs.myPreview.setIsShowPreview(true);
      this.$store.dispatch("setPreviewId", this.$refs.myPreview);
    },

    /**
     * 图片添加行为
     **/
    addAction() {
      this.$emit("imageAddAction");
    },

    /**
     * 删除指定的图片
     * @param
     * picIndex     Number    当前要删除图片的索引
     **/
    deletePicture(picIndex) {
      this.selectPictures.splice(picIndex, 1);
      console.log(this.selectPictures);
    },

    /**
     * 选择当前图片来源方式(拍照或本地上传)
     * @param
     * item      Object   当前选择的方式对象
     * index     Number   当前选择的索引
     **/
    addSelect(item, index) {
      index ? this.getPicture() : this.useCamera();
    },

    /**
     * 调用摄像头插件进行拍照
     * @param
     **/
    useCamera(needCropImg = true) {
      this.$plugin.usePlugin("camera")(
        needCropImg,
        imageData => {
          if (needCropImg && this.$devicePlatform == "android") {
            //裁剪图片
            this.$plugin.usePlugin("crop")(imageData,
              url => {
                //success callback
                console.log("yuanl=> 裁剪url = "+url);
                url = url.split('?');
                this.selectPictures.push(url[0]);
              },
              error => {
                //fail callback
                console.log("Crop error. "+JSON.stringify(error));
                if (error.recapture == "true") {
                  console.log("调用相机重拍. ");
                  this.useCamera(needCropImg);
                } else if (error.message == "User cancelled") {
                  console.log("用户取消裁剪操作. ");
                } else {
                  this.$toast("裁剪图片出现异常，请重试！");
                }
              }
            );
          } else {
            this.selectPictures.push(imageData);
          }
        },
        error => {
          //Toast(error); //delete for ZHSQYYPT-5889
        }
      );
    },

    /**
     * 上传图片数据
     * @param
     * fileURL  String  图片本地资源路径
     **/
    uploadFile() {
      this.uploadImageList = [];
      let reqUrl = `${store.get(
        "custUrl.homeUrl"
      )}xwj-sys-media/media/upload/uploadFile`;
      var options = {};
      options.params = {
        file: "",
        source: "yzApp",
        business: "dicoveryPic"
      };
      options.headers = { contentType: "multipart/form-data" };
      var totalNum = this.selectPictures.length;
      var num = 0;
      console.log("开始调用图片上传");
      if (this.selectPictures.length == 0) {
        this.$emit("imageMapChange", []);
      }
      console.log(this.selectPictures);
      this.loading = store.get("showLoadingView");
      if (this.loading) {
          this.loading(true);
      }
      this.selectPictures.forEach(item => {
        options.params.file = item;
        this.$plugin.usePlugin("fileTransfer")(
          reqUrl,
          item,
          options,
          result => {
            let resData = JSON.parse(result.response);
            if (resData.resultCode == 0) {
              this.uploadImageList.push(resData.data);
            }
            num++;
            console.log(totalNum);
            console.log(num);
            this.dealResult(totalNum == num);
          },
          error => {
            num++;
            console.log(totalNum);
            console.log(num);
            this.dealResult(totalNum == num);
          }
        );
        // ft.upload(
        //   item,
        //   encodeURI(reqUrl),
        //   (result) => {
        //     let resData = JSON.parse(result.response);
        //     if (resData.resultCode == 0) {
        //       this.uploadImageList.push(resData.data);
        //     }
        //     num++;
        //     console.log(totalNum);
        //     console.log(num);
        //     this.dealResult(totalNum == num);
        //   },
        //   () => {
        //     num++;
        //     console.log(totalNum);
        //     console.log(num);
        //     this.dealResult(totalNum == num);
        //   },
        //   options
        // );
      });
    },

    /**
     * 将上传的图片返回
     **/
    dealResult(flag) {
      if (this.loading) {
        this.loading(false);
      }
      console.log(this.uploadImageList);
      if (flag) {
        this.$emit("imageMapChange", this.uploadImageList);
      }
    },

    /**
     * 从本地相册进行上传
     **/
    getPicture() {
      this.$plugin.usePlugin("getPictures")(
        this.maximumImagesCount,
        images => {
          images.forEach(imageData => {
            this.selectPictures.push(imageData);
          });
        },
        error => {
          //Toast(error); //delete for ZHSQYYPT-5889
        }
      );
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.image-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  > li {
    position: relative;
    width: 25%;
    flex-shrink: 0;
    padding: 3px;
    box-sizing: border-box;
    .delete-picture {
      width: 40px;
      height: 40px;
      position: absolute;
      left: 50%;
      top: 50%;
      margin-left: -20px;
      margin-top: -20px;
    }
    > img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .add-image {
      width: 85px;
      height: 85px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #f0f0f0;
    }
  }
  /deep/.van-image-preview__index {
    left: 0 !important;
    top: 0 !important;
    right: 0 !important;
    height: 46px !important;
    background: #ffffff;
  }
  .tab-index {
    width: 100%;
  }
}
</style>
